.main{
    height: 3rem;
    width: 170px;
    text-align: center;
    border: none;
    border-bottom: 1px solid gray;
    color: gray;
    background-color: rgba(0,0,0,0.02);
    font-weight: 800;
}

.persian_font{
    font-family: hekayat !important;
    font-size: 25px;
    letter-spacing: 2px;
}


.main:hover{
    cursor: pointer;
}

.main:focus{
    outline: none;
    background-color: rgba(100,100,100,0.3)
}